<template>
   <div class="navbar">
      <v-navigation-drawer 
         v-model="drawer"
         absolute 
         floating
      >
         <v-list
         nav
         >
            <v-list-item-group
               v-model="group"
               active-class="deep-purple--text text--accent-4"
            >
               <v-list-item>
                  <router-link :to="{name: 'Home'}" class="black--text text-decoration-none">
                     <v-list-item-title>Inicio</v-list-item-title>
                  </router-link>
               </v-list-item>

               <v-list-item>
                  <router-link :to="{name: 'About'}" class="black--text text-decoration-none">
                     <v-list-item-title>Sobre mí</v-list-item-title>
               </router-link>
               </v-list-item>

               <v-list-item>
                  <router-link :to="{name: 'Works'}" class="black--text text-decoration-none">
                     <v-list-item-title>Portafolio</v-list-item-title>
               </router-link>
               </v-list-item>

               <v-list-item>
                  <router-link :to="{name: 'Contact'}" class="black--text text-decoration-none">
                     <v-list-item-title>Contacto</v-list-item-title>
               </router-link>
               </v-list-item>
            </v-list-item-group>
         </v-list>
      </v-navigation-drawer>

      <v-toolbar 
         :height="toolbarheightProp"
         :color="toolbarcolorProp"
         dark absolute
         style="position: fixed; width: 100%; z-index: 20; transition: background-color 0.5s ease-in-out, height 0.5s ease-in-out;"
      >
         <v-toolbar-title class="d-xs-none">
            <router-link :to="{name: 'Home'}" class="text-decoration-none">
               <v-avatar color="#FF9D8E" size="62" tile style="border: 1px solid white; position: absolute; top: 5px">
                  <span class="white--text headline">JV</span>
               </v-avatar>
            </router-link>
         </v-toolbar-title>

         <v-spacer> </v-spacer>

         <v-tabs class="d-none d-md-block"
         align-with-title 
         light
         background-color="transparent"
         color="white"
         >
            <v-tab :to="{name: 'Home'}">
               Inicio
            </v-tab>
            <v-tab :to="{name: 'About'}">
               Sobre mí
            </v-tab>
            <v-tab :to="{name: 'Works'}">
               Portafolio
            </v-tab>
            <v-tab :to="{name: 'Contact'}">
               Contacto
            </v-tab>
         </v-tabs>

         <v-app-bar-nav-icon 
         class="d-block d-md-none" 
         @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

      </v-toolbar>
   </div>
</template>

<script>
   export default {
      name: 'Navbar',
      data: () => ({
            drawer: false,
            group: null,
            toolbarheight: '50'
      }),
      props: {
         toolbarcolorProp: {
            type: String
         },
         toolbarheightProp: {
            type: Number
         }
      },
      watch: {
         group () {
         this.drawer = false
         },
      },
   }
</script>

<style lang="scss">
.v-navigation-drawer--absolute {
   z-index: 51 !important;
}

.v-toolbar__title {
   overflow: unset !important;
}
.v-slide-group__content {
   justify-content: flex-end !important;
}

</style>