<template>
  <header class="Hero">
      <v-row class="Hero__content justify-content-center col-xs-12 col-sm-10 col-md-7 col-lg-7 col-xl-7">
         <v-col cols="12" class="mt-6">
            <v-img class="mx-auto"
               contain
               lazy-src="../assets/jv_title.png"
               max-height="250"
               max-width="700"
               src="../assets/jv_title.png"
            ></v-img>
         </v-col>
         <v-col cols="12">
            <div class="typewriter mx-auto" style="width: 17em">
               <h1 class="text-center">Hello world, from Jenny.</h1>
            </div>
         </v-col>
         <v-col cols="12">
            <div class="arrow-container mx-auto mb-2" @click="$vuetify.goTo(target, options)">
               <div class="chevron"></div>
               <div class="chevron"></div>
               <div class="chevron"></div>
            </div>
         </v-col>
      </v-row>
   </header>
</template>

<script>
import * as easings from 'vuetify/es5/services/goto/easing-patterns'
   export default {
      name: 'Hero',
      data () {
         return {
         type: 'number',
         number: 460,
         duration: 500,
         offset: 0,
         easing: 'easeInOutCubic',
         easings: Object.keys(easings),
         }
      },
      computed: {
         target () {
            const value = this[this.type]
            if (!isNaN(value)) return Number(value)
            else return value
         },
         options () {
            return {
               duration: this.duration,
               offset: this.offset,
               easing: this.easing,
            }
         },
      },
   }
</script>

<style lang="scss">
$azulmarino: #373741;
$coral: #ff9d8e;
$darkcoral: #ff654e;

.Hero {
   display: flex;
   align-items: center;
   justify-content: center;
   height: 70vh;
   background: url('../assets/Sun-Tornado.svg') repeat-y;
   background-position-y: top;
   background-size: cover;
}

/* Google Fonts */
@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
// efecto typewriter
.typewriter h1 {
   font-family: 'Anonymous Pro', monospace;
   text-align: center;
   font-size: 90%;
   width: 17em;
   color: white;
   overflow: hidden; /* Ensures the content is not revealed until the animation */
   border-right: .15em solid $darkcoral; /* The typewriter cursor */
   white-space: nowrap; /* Keeps the content on a single line */
   margin: 0 auto; /* Gives that scrolling effect as the typing happens */
   letter-spacing: .15em; /* Adjust as needed */
   animation: 
      typing 2.5s steps(40, end),
      blink-caret .75s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #2d2d2e; }
}

// flechas scroll
.arrow-container {
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 8;
}

.chevron {
  position: absolute;
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: ' ';
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
}

.chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;

  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

</style>